.labeled-input-box {
    /* border: 1px solid black; */
    width: 50px;
    height: 40px;
    text-align: center;
    border: none;
    border-top: 1px solid black;
    border-left: 1px solid black;
}

.labeled-input-name {
    position: absolute;
    color: black;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    text-align: center;
    width: 100%;
    /* border: 1px solid black; */
    border: none;
    border-bottom: none;
    font-size: 11px;
    cursor: pointer;
}

.labeled-input {
    background-color: transparent;
    border: none;
    width: max-content;
    position: relative;
}
